@import "../../common/styles/sass/mixins";
@import "../../common/styles/sass/variables";

.topMenu {
  margin-right: 50px;
  float: right;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    height: 80px;
    display: flex;
    align-items: center;

    li {
      margin-left: 30px;
      display: inline-block;
      vertical-align: top;
      position: relative;

      a {
        @include text($fontMain, 12px, $textColor);
        text-transform: uppercase;
        font-weight: 800;
        cursor: pointer;

        &:hover {
          color: $primaryColor;
        }

        span {
          position: relative;
          top: -1px;
          display: inline-block;
          vertical-align: middle;
          line-height: 16px;
        }
      }

      .active {
        color: $primaryColor;
      }
    }
  }
}


@media screen and (max-width: 1280px) {
  .topMenu {
    margin-right: 30px;
  }
  .topMenu ul li {
    margin-left: 25px;
  }
}

@media screen and (max-width: 992px) {
  .topMenu {
    display: none;
  }
}
